@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*article meta*/
@mixin article-meta-text-size() {
	@include font(1.4rem, round-float(18/14, 2)); }

.article-meta {
	color: $text-color-light;
	margin-bottom: $bottom-space-md;
	@include clear;

	@include media('<desk-hd') {
		margin-bottom: 1rem; }

	@include media('<mob-lg') {
		margin-bottom: 0.5rem; } }

/*article meta end*/

/*date*/
.datetime {
	float: left;
	display: block;
	margin-bottom: $bottom-space;
	@include font-bold;
	@include article-meta-text-size;
	color: $base-color;
	font-style: normal;

	@include media('<desk-hd') {
		margin-bottom: 1rem; }

	@include media('<mob-lg') {
		margin-bottom: 0.5rem; }

	.article-meta & {
		margin-right: 20px;
		margin-bottom: 0; } }

/*date end*/

/*visit*/
.visit-view {
	svg {
		@include display-inline-block-middle;
		@include size(18px);
		margin-right: 10px;

		path {
			fill: currentColor;
			transition-property: fill;
			transition-duration: $animation-speed; }

		&.depict-visit-share {
			@include size(12px); } }

	& > em {
		@include display-inline-block-middle;
		font-style: normal; } }

.visit {
	overflow: hidden;
	margin-left: 20px;
	text-align: right;
	@include article-meta-text-size;
	color: $text-color-light;
	//cursor: default
	display: none; }

.visit__item {
	display: inline-block;
	vertical-align: top;
	margin-left: 30px;

	@include media('<mob-lg') {
		margin-left: 5px; } }

.visit__inner {
	display: inline-block;
	text-decoration: none;
	color: $text-color-light; }

/*visit end*/
