@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*save info*/
.save {
	display: block;
	position: relative;
	//margin-bottom: $grid-space
	height: $news-item-height; // Хардкод. Высота должна быть равно высоте элемета новостей
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	text-decoration: none;
	overflow: hidden;

	@include media('<desk-hd') {
		height: $news-item-lg-img-height - $events-summary-nav-height - $grid-space; } // Хардкод. Высота должна равняться высоте изображения (или слайдера) большой новости минус высота переключателей табов блока ниже

	@include media('<desk-lg') {
		height: ($news-item-lg-height - $news-item-lg-img-height + $events-summary-nav-height); } // Хардкод. Высота должна равняться высоте большой новости минус

	@include media('<mob-lg') {
		height: auto;
		@include padding-vertical(10px); }

	//&::before
	//include pseudo($pos: relative)
	//include ratio(1, 1)

	&::after {
		@include pseudo();
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: $text-color-alt;
		opacity: 0.65; } }

.save__header {
	font-size: 14px;
	text-align: center;
	padding: 1rem;
	font-weight: 700;
	color: #fff;
	line-height: 1.1;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 5;

	@include media('<mob-lg') {
		display: block;
		position: relative;
		top: auto;
		left: auto;
		width: auto;
		padding-bottom: 0; } }

.save__holder {
	position: absolute;
	left: 0;
	top: 4rem;
	width: 100%;
	height: calc(100% - 4rem);
	z-index: 1;
	@include padding-horizontal($grid-inside-horizontal-space);

	@include media('<mob-lg') {
		position: relative;
		left: auto;
		top: auto;
		height: auto; } }

.save__box {
	height: 50%;
	position: relative;
	border-top: 1px solid #fff;

	&:last-child {
		height: 100%; }

	& ~ .save__box {
		height: 50%; }

	@include media('<mob-lg') {
		height: auto;
		@include padding-vertical(10px); } }

.save__box:first-child {
	border-top: none; }

.save__align {
	position: absolute;
	left: 50%;
	top: 50%;
	width: 100%;
	color: white;
	text-align: center;
	transform: translate(-50%, -50%);
	@include antialiased;
	z-index: 5;

	@include media('<mob-lg') {
		position: relative;
		left: auto;
		top: auto;
		transform: none; }

	& > strong, & > span {
		display: block;
		font-family: $font-alt;
		font-weight: 700; }

	& > strong {
		font-size: 3.6rem;
		line-height: 1.1; }

	& > span {
		font-size: 1.8rem;
		line-height: 1.3; } }

/*save info end*/
