@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*lead */
.lead {
	position: relative;
	background-color: white;
	overflow: hidden;
	margin-bottom: $bottom-space;

	&:last-child {
		margin-bottom: 0; } }

.lead__description {
	padding: $grid-inside-vertical-space $grid-inside-horizontal-space;
	border-bottom: 1px solid $border-color;
	overflow: hidden;

	p:last-child {
		margin-bottom: 0; } }

.lead__list {
	margin-left: -1px;
	margin-bottom: -1px;
	margin-right: -1px;
	@include clear; }

.lead__item {
	position: relative;
	float: left;
	width: 33.33%;
	padding: 0 0 1px 1px;
	text-align: center;

	@include media('<mob-lg') {
		width: 100%; } }

.lead__layer, .lead__layer--front, .lead__layer--back {
	position: relative;
	padding: $grid-inside-vertical-space $grid-inside-horizontal-space 80px;
	background: white;
	box-shadow: -1px 0 0 $border-color, 1px 0 0 $border-color, 0 1px 0 $border-color, 0 -1px 0 $border-color;
	min-height: 100%;

	transition-property: opacity, visibiltiy;
	transition-duration: $animation-speed; }

.lead__layer--front {

	.fb-switcher-active & {
		opacity: 0;
		visibility: hidden; } }

.lead__layer--back {
	position: absolute;
	left: 1px;
	top: 0;
	right: 0;
	bottom: 1px;
	background-color: #fff;
	min-height: calc(100% - 1px);

	opacity: 0;
	visibility: hidden;

	.no-js & {
		opacity: 1;
		visibility: visible;
		position: relative;
		left: auto;
		top: auto;
		right: auto;
		bottom: auto; }

	.fb-switcher-active & {
		opacity: 1;
		visibility: visible; } }

.lead__position {
	@include font(1.6rem, 1.2);
	color: $text-color-light;
	margin-bottom: $bottom-space-sm; }

.lead__name {
	font-family: $font-alt;
	font-weight: bold;
	@include font(1.8rem, 1.3);
	margin-bottom: $bottom-space-sm; }

.lead__footer {
	position: absolute;
	left: 0;
	width: 100%;
	bottom: $grid-inside-vertical-space;

	.no-js & {
		display: none; }

	a {
		color: $text-color-alt;
		text-decoration: none;

		span {
			border-bottom: 1px dotted $text-color-alt;
			transition-property: border-color;
			transition-duration: $animation-speed; }

		.no-touchevents &:hover span {
			border-bottom-color: transparent; } } }

.lead__schedule {
	text-align: left;
	@include antialiased;

	dl {
		padding: 0;
		margin: 0 0 $bottom-space-sm;

		&:last-child {
			margin-bottom: 0; }

		dt, dd {
			margin: 0;
			padding: 0; }

		dt {
			float: left;
			margin-right: 6px;
			@include font-bold; } } }

/*lead end */
