@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*social media */
.soc-media {
	position: relative;
	@include clear;

	&::before {
		@include pseudo();
		@include top-left(0, 50%);
		bottom: 0.15em;
		border-right: 1px solid $border-color;

		@include media('<mob-lg') {
			content: none; } } }

.soc-media__col {
	position: relative;

	&:first-child {
		float: left;
		width: calc(50% - #{$grid-inside-horizontal-space});

		@include media('<mob-lg') {
			float: none;
			width: 100%; } }

	&:nth-child(2) {
		margin-left: calc(50% + #{$grid-inside-horizontal-space});

		@include media('<mob-lg') {
			margin-left: 0; } }

	a {
		display: block;
		text-decoration: none;
		color: $text-color; } }

.soc-media__visual {
	margin-bottom: $bottom-space;
	padding-bottom: $bottom-space;
	border-bottom: 1px solid $border-color;

	.video {
		padding-bottom: 26px;
		background: url(../../img/bg-video.png) no-repeat center bottom;
		background-size: 100%;
		margin-bottom: 0;

		@include media("<desk-hd") {
			padding-bottom: 40px; }

		@include media("<desk-lg") {
			padding-bottom: 26px; }

		@include media("<mob-lg") {
			padding-bottom: 40px; }

		@include media("<mob-md") {
			padding-bottom: 26px; } }

	.video-holder {
		padding-bottom: 62.5%;
		padding-top: 25px; } }

.soc-media__footer {
	padding-top: $bottom-space;

	@include media("<desk-hd") {
		padding-top: 0; }

	@include media("<desk-lg") {
		padding-top: $bottom-space; }

	@include media('<mob-lg') {
		padding-top: 0;
		padding-bottom: $bottom-space; } }

.soc-media-share {
	text-align: center; }

.soc-media-share__label {
	text-align: center;
	font-family: $font-alt;
	@include font(1.2rem, 1.6);
	color: $text-color-light;
	text-transform: uppercase;
	margin-bottom: $bottom-space-sm; }

.soc-media-share {

	.social-networks__list {

		@include media("<desk-hd") {
			@include margin-horizontal(-15px); }

		@include media("<desk-lg") {
			@include margin-horizontal(-5px); }

		@include media("<mob-lg") {
			@include margin-horizontal(-15px); }

		@include media("<mob-md") {
			@include margin-horizontal(-5px); } }

	.social-networks__item {
		@include media("<desk-hd") {
			@include margin-horizontal(15px); }

		@include media("<desk-lg") {
			@include margin-horizontal(5px); }

		@include media("<mob-lg") {
			@include margin-horizontal(15px); }

		@include media("<mob-md") {
			@include margin-horizontal(5px); }

		a {
			background: none;
			@include size(32px);

			.no-touchevents &:hover {
				transform: none;
				box-shadow: none; }

			svg {
				@include size(22px); }

			path {
				fill: $text-color-light; }

			.no-touchevents &.fb:hover {

				path {
					fill: $facebook-color; } }

			.no-touchevents &.twitter:hover {

				path {
					fill: $twitter-color; } }

			.no-touchevents &.vk:hover {

				path {
					fill: $vkontakte-color; } }

			.no-touchevents &.ok:hover {

				path {
					fill: $odnoklassniki-color; } }

			.no-touchevents &.instagram:hover {

				path {
					fill: $instagram-color-end; } } } } }

.soc-media__img {
	height: 227px;
	background-position: center;
	background-repeat: no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
	margin-bottom: $bottom-space;

	@include media("<desk-hd") {
		height: 0;
		padding-bottom: 100%; }

	@include media("<desk-lg") {
		height: 227px; } }

.soc-media__text {
	@include font($font-size-small, $line-height-small);
	overflow: hidden;
	height: round-float($font-size-small*$line-height-small*4, 1);
	margin-bottom: $bottom-space;

	@include media("<desk-hd") {
		height: auto; }

	@include media("<desk-lg") {
		height: round-float($font-size-small*$line-height-small*4, 1); } }

.soc-media__link {
	font-family: $font-alt;
	font-weight: 700;
	@include font(1.6rem, 1.25);
	@include truncate(100%);
	transition-property: color;
	transition-duration: $animation-speed;

	.no-touchevents .soc-media__col a:hover & {
		color: $text-color-alt; } }

/*social media end */
