@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*ctrl*/
.ctrl {
	.article-layout &:last-child {
		margin-bottom: -#{$article-layout-space-vertical};

		@include media('<desk-hd') {
			margin-bottom: -#{$article-layout-space-vertical--desk-hd}; }

		@include media('<tab-lg') {
			margin-bottom: -#{$article-layout-space-vertical--tab-lg}; }

		@include media('<mob-lg') {
			margin-bottom: -#{$article-layout-space-vertical--mob-lg}; } } }

.ctrl__holder {
	@include clear; }

.ctrl__buttons {
	float: left;
	@include clear;

	@include media('<desk-hd') {
		float: none;
		margin-bottom: $bottom-space; }

	& > a {
		float: left;
		margin-right: $grid-layout-space--desk-hd;
		margin-bottom: 0;

		&:nth-child(3) {
			clear: left;

			&, & ~ a {
				margin-top: 20px; } }

		&.view-print {
			@include media('<desk-hd') {
				float: right;
				@include margin-horizontal($grid-layout-space--desk-hd, 0); }

			@include media('<tab-lg') {
				margin: 0; }

			@include media('<mob-md') {
				float: none; } }

		@include media('<tab-lg') {
			margin: 0; }

		@include media('<mob-md') {
			float: none; } }

	& > [class*="btn-"] {
		max-width: 270px;

		@include media('<mob-md') {
			display: block;
			max-width: none;
			margin-bottom: $bottom-space;

			&:last-child {
				margin-bottom: 0; } } } }

.ctrl .share-box {
	float: right;

	.share-box__label {
		@include font(1.6rem, 1.25);
		display: inline-block;
		vertical-align: top;
		max-width: 100px; }

	.social-networks {
		display: inline-block;
		vertical-align: top;

		li {
			@include margin(null, 0, null, 10px); } }

	@include media('<mob-lg') {
		float: none;

		.share-box__label {
			float: none;
			margin-bottom: $bottom-space-sm;
			max-width: none; }

		.social-networks {
			display: block; } } }

/*ctrl end*/
