
@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/*section*/
.section {
	margin-bottom: $bottom-space-md;

	@include media('<desk-lg') {
		margin-bottom: $bottom-space; }

	&:last-child {
		margin-bottom: 0; }

	* ~ & {
		margin-top: $bottom-space-md; }

	script ~ &, input ~ & {
		margin-top: 0; }

	& > *:last-child {
		margin-bottom: 0; } }

.section__title {
	position: relative;
	font-family: $font-alt;
	margin-bottom: $bottom-space-sm;
	color: $text-color;
	font-size: 2rem;
	line-height: 1.3;

	.home-page & {
		color: $text-color-alt; }

	h2, h3, & > a {
		@include display-inline-block-middle;
		position: relative;
		font-family: inherit;
		color: inherit;
		@include font(inherit, inherit);
		margin: 0;

		@include media('<mob-lg') {
			display: block;
			margin-bottom: (round-float($bottom-space-sm*0.5, 1)); }

		& > a {
			font-weight: inherit; } }

	&.has-date {
		h2, h3 {
			@include media('<mob-lg') {
				display: inline-block;
				margin-right: 4px;
				margin-bottom: 0; } } }

	h2, h3 {
		vertical-align: top; }

	& > a {
		font-size: 0.9em;
		font-weight: normal;
		top: 0.1em;
		cursor: pointer;

		span {
			border-bottom: 1px solid transparent;
			transition-property: border-color;
			transition-duration: $animation-speed; }

		.no-touchevents a:hover {
			text-decoration: none; }

		&.type-anchor {
			text-decoration: none;

			span {
				border-bottom: 1px dotted $text-color; }

			.no-touchevents &:hover span {
				border-bottom-color: transparent; } } }

	.v-separator {
		font-size: 0.6em;
		vertical-align: middle;

		& + a {
			color: $text-color;
			text-transform: uppercase;
			@include font(0.6em, 1);
			vertical-align: middle; } }

	.touchevents & .news-date {
		display: inline-block;
		vertical-align: middle;
		line-height: 1;

		& ~ a {
			margin-bottom: 0; } } }

.v-separator {
	@include display-inline-block-middle;
	@include margin-horizontal(10px);

	@include media('<mob-lg') {
		display: none;

		.news-date + & {
			display: inline-block; } }

	.section__title & {
		color: $text-color; }

	& + a {
		@include display-inline-block-middle;
		top: auto; } }

.section__box {
	background-color: white;
	padding: $grid-inside-horizontal-space;

	@include media('<mob-lg') {
		padding: $grid-layout-space--mob-lg; } }

.datepicker-input {
	position: relative;
	top: 0.1em;
	@include display-inline-block-middle;
	padding: 0;
	margin: 0;
	font-family: $font-alt;
	@include font(0.9em, 23px);
	height: 23px;
	font-weight: normal;
	cursor: pointer;
	border: 1px solid $text-color;
	background: white;

	@include media('>=desk-lg') {
		vertical-align: top;
		height: 20px;
		line-height: 20px; }

	//&::-webkit-inner-spin-button, &::-webkit-calendar-picker-indicator
	//webkit-appearance: none
	//isplay: none

	.no-touchevents .datepicker-alt & {
			position: absolute;
			left: 0;
			bottom: 0;
			top: auto;
			width: 100%;
			height: 0;
			opacity: 0;
			padding: 0;
			border: 0;
			margin: 0 0 -5px; } }

.section__subtitle {
	margin-bottom: $bottom-space-sm;
	font-family: $font-alt;
	font-weight: normal;
	@include font(1.2rem, 1.6);
	text-transform: uppercase;
	color: $text-color;

	h2, h3, h4 {
		@include display-inline-block-middle;
		font-weight: inherit;
		font-family: inherit;
		@include font(inherit, inherit);
		color: inherit;
		margin-bottom: 0;

		@include media('<mob-lg') {
			font-size: 1.4rem;
			display: block;
			margin-bottom: ($bottom-space-sm); } }

	a {
		text-decoration: underline;
		color: inherit;

		.no-touchevents a:hover {
			text-decoration: none; } } }

.hide-tab-lg {
	display: none;

	@include media('<desk-lg') {
		display: block; } }

.filter-item {
	max-width: 320px; }

/*section end*/
