	//+size(auto)
	@include display-inline-block-top;
	position: relative;
	cursor: pointer;
	line-height: 18px;
	@include antialiased;

	.no-js & {
		display: none; }

	& > span {
		@include display-inline-block-top;
		position: relative;
		font-family: $font-alt;
		text-transform: uppercase;
		@include font(0, 0);
		font-weight: normal;
		white-space: nowrap;

		//&:before, &:after
		//isplay: none
		//osition: relative
		//ransition-property: opacity, transform, visibility
		//ransition-duration: $animation-speed

		&:before, &:after {
			@include display-inline-block-top;
			@include font(12px, 18px);
			position: relative;
			border-bottom: 1px dotted $text-color;
			transition-property: opacity, transform, visibility;
			transition-duration: $animation-speed; }

		&:before {
			content: 'Показать ответ';
			transform: translate(0, 0);

			.expander-is-open & {
				opacity: 0;
				visibility: hidden;
				transform: translate(0, -1em); } }

		&:after {
			content: 'Закрыть ответ';
			position: absolute;
			@include top-left(0);
			@include size(100%);
			opacity: 0;
			visibility: hidden;
			width: auto;
			transform: translate(0, 1em);

			.expander-is-open & {
				opacity: 1;
				visibility: visible;
				transform: translate(0, 0); } }

		.no-touchevents &:hover {
			color: $text-color-alt;

			&:before, &:after {
				border-bottom-color: $text-color-alt; } } }

	& > i {
