.expander-panel {
	position: relative;
	overflow: hidden;
	border-top: 1px solid $border-color;
	display: none;
	@include font($font-size-small, $line-height-small);

	//+media('<mob-lg')
	//order-top: none

	.no-js & {
		display: block; } }

.expander-panel__holder {
	padding: $grid-inside-vertical-space 0;

	@include media('<tab-lg') {
		@include padding-vertical($article-layout-space-vertical--tab-lg); }

	@include media('<mob-lg') {
		@include padding-vertical($article-layout-space-vertical--mob-lg); } }

/*expander end */

/*faq form */
.faq-form-wrap {
	margin-bottom: $bottom-space-lg;

	&:last-child {
		margin-bottom: 0; } }

/*faq form end */
